<template>
    <div>
        <div>
            <!-- 顶部 -->
            <div class="minetop">
                <div class="set-up">
                    <router-link :to="{name:'setup'}">
                        <van-tabbar v-model="active">
                            <van-tabbar-item icon="setting-o"></van-tabbar-item>
                        </van-tabbar>
                    </router-link>
                </div>
                <div class="me" v-if="mydatas.unickname">
                    <img :src="imgpath+mydatas.uavatar" :alt="mydatas.unickname">
                </div>
                <p>{{mydatas.unickname}}</p>
            </div>
            <!-- 金刚区 -->
            <ul class="mine-jg">
                <li>
                    <!--<img src="" alt="">-->
                    <p>待付款</p>
                </li>
                <li>
                    <!--<img src="../../assets/mine/set-out-goods.png" alt="">-->
                    <p>待发货</p>
                </li>
                <li>
                    <!--<img src="../../assets/mine/harvested.png" alt="">-->
                    <p>待收货</p>
                </li>
                <li>
                    <!--<img src="../../assets/mine/evaluated.png" alt="">-->
                    <p>待评价</p>
                </li>
                <li>
                    <!--<img src="../../assets/mine/my-coupon.png" alt="">-->
                    <p>全部订单</p>
                </li>
            </ul>

            <!-- 优惠券 -->
            <a href="">
                <div class="mine-youhui">
                    <img src="" alt="">
                    <span>我的优惠券</span>
                </div>
            </a>

            <a href="">
                <div class="mine-youhui">
                    <img src="" alt="">
                    <span>收货地址</span>
                </div>
            </a>

            <a href="">
                <div class="mine-youhui">
                    <img src="" alt="">
                    <span>售后服务</span>
                </div>
            </a>
            <!-- 退出登录 -->
            <router-link :to="{name:'login'}">
                <div class="mine-tui">
                    退出登录
                </div>
            </router-link>
        </div>
        <z-footer :active="4"></z-footer>
    </div>
</template>

<script>
    import ZFooter from '../../components/z-footer/z-footer';
    import {HOSTNAME,SUCCESS,IMGPATH} from "../../config/base";
    import {getmydata} from '../../api/users';

    export default {
        name: "Mine",
        data() {
            return {
                active: 0,
                mydatas:[],
                imgpath:IMGPATH
            }
        },
        methods: {
            getmydatas() {
                getmydata(HOSTNAME + '/api/perdata/'+this.$store.state.uid)
                    .then(res=>{
                        let {data,code,msg}=res.data;
                        if (code===SUCCESS) {
                            this.mydatas=data;
                            console.log(msg);
                        }else{
                            console.log(msg);
                        }
                    })
            }
        },
        computed: {
            token() {
                return this.$store.state.token;
            }
        },
        components: {
            ZFooter
        },
        beforeMount() {
            this.getmydatas();
        }
    }
</script>

<style scoped>
    @import url("../../style/mine.css");

    .van-tabbar--fixed {
        position: absolute;
        right: 0;
        top: 0;
    }

    .van-tabbar {
        background-color: rgba(255, 255, 255, 0);
        width: 100%;
        height: 100%;
    }

    .van-tabbar-item {
        font-size: 0.24rem;
    }

    .van-tabbar-item__icon {
        font-size: 0.24rem;
    }
</style>